import { Card, Form, FormGroup, Label, Input, Button } from 'reactstrap'
import React from 'react'

export default function MenuDeJours() {
  const styleCard = {
    backgroundColor: '#dcdcdc',
    marginBottom: '20px',
    padding: '20px',
    marginTop: '50px',
  }

  const AjoutMenu = () => {
    fetch('api/Menu/AjouterMenu', {
      method: 'POST',
      headers: {
        Accept: 'application/json, application/xml,text/plain, text/html, *.*',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(),
    })
      .then((response) => response.json())
      .then((response) => {
        console.log(' handleChange response ' + JSON.stringify(response))
      })
      .catch((err) => {
        console.log('fetch failed =>', err)
      })
  }

  return (
    <Card style={styleCard}>
      <div>
        <h1 style={{ fontWeight: 'bold', textAlign: 'center' }}>
          Ajouter une menu{' '}
        </h1>
        <h2 style={{ fontFamily: 'serif' }}>Créer une nouvelle recette </h2>
        <Form>
          <FormGroup>
            <Label for="Titre">Titre </Label>
            <Input type="text" name="text" id="text" />
          </FormGroup>
          <FormGroup>
            <Label for="Titre">Description </Label>
            <Input type="textarea" name="text" id="textarea" />
          </FormGroup>
          <FormGroup>
            <Label for="Titre">Ingrédients</Label>
            <Input type="textarea" name="text" id="textarea" />
          </FormGroup>
          <Button onChange={AjoutMenu}>Ajouter</Button>
        </Form>
      </div>
    </Card>
  )
}
