import React, { useState } from 'react'
import './Menu.css'
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from 'reactstrap'

const items = [
  {
    src:
      'https://t4.ftcdn.net/jpg/02/18/63/35/240_F_218633582_ZscwusonrbMUlAolM70ExoJx4Oi6NHkn.jpg',
    altText: 'Slide 1',
    caption: 'Slide 1',
  },
  {
    src:
      'https://image.freepik.com/photos-gratuite/salade-mixte-aux-fruits-mer-crabes-champignons-legumes-verts_114579-1800.jpg',
    altText: 'Slide 2',
    caption: 'Slide 2',
  },
  {
    src:
      'https://t3.ftcdn.net/jpg/03/42/82/74/240_F_342827401_7ZkDx2lfm0FIeSDJYz6qM4NgXpv5rEto.jpg',
    altText: 'Slide 3',
    caption: 'Slide 3',
  },
]

const Caroussel = (props) => {
  const [activeIndex, setActiveIndex] = useState(0)
  const [animating, setAnimating] = useState(false)

  const next = () => {
    if (animating) return
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1
    setActiveIndex(nextIndex)
  }

  const previous = () => {
    if (animating) return
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1
    setActiveIndex(nextIndex)
  }

  const goToIndex = (newIndex) => {
    if (animating) return
    setActiveIndex(newIndex)
  }
  const slides = items.map((item) => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <img src={item.src} alt={item.altText} width="800" height="400" />
        <CarouselCaption
          captionText={item.caption}
          captionHeader={item.caption}
        />
      </CarouselItem>
    )
  })
  return (
    <div>
      <Carousel activeIndex={activeIndex} next={next} previous={previous}>
        <CarouselIndicators
          items={items}
          activeIndex={activeIndex}
          onClickHandler={goToIndex}
        />
        {slides}
        <CarouselControl
          direction="prev"
          directionText="Previous"
          onClickHandler={previous}
        />
        <CarouselControl
          direction="next"
          directionText="Next"
          onClickHandler={next}
        />
      </Carousel>
    </div>
  )
}

export default Caroussel
