import React from 'react'
import { Table, Card } from 'reactstrap'

const AjoutIngrédient = (props) => {
  const StyleTable = {
    border: '1px',
    width: '100%',
  }
  const StyleButtonUpdate = {
    marginRight: '10px',
  }
  const styleCard = {
    backgroundColor: '#fffafa',
    marginBottom: '20px',
    padding: '20px',
    marginTop: '50px',
  }

  const ModifierIngredient = () => {
    fetch('api/Ingrédient/ModifierIngrédient', {
      method: 'POST',
      headers: {
        Accept: 'application/json, application/xml,text/plain, text/html, *.*',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(),
    })
      .then((response) => response.json())
      .then((response) => {
        console.log(' handleChange response ' + JSON.stringify(response))
        this.setState({})
      })
      .catch((err) => {
        console.log('fetch failed => ', err)
      })
  }
  {
    /* btnSupprimeIngredient = () => { } */
  }

  return (
    <div>
      <Card style={styleCard}>
        <h1
          style={{
            textAlign: 'center',
            fontWeight: 'bold',
            paddingBottom: '20px',
          }}
        >
          Gérer les ingrédients
        </h1>
        <Table hover style={StyleTable}>
          <thead style={{ fontSize: '18px' }}>
            <tr>
              <th>ID</th>
              <th>Titre</th>
              <th>Quantité</th>
              <th style={{ width: '35%' }}>Modifier</th>
            </tr>
          </thead>
          <tbody style={{ fontSize: '16px' }}>
            <tr>
              <th scope="row">1</th>
              <td>tomate</td>
              <td>2</td>
              <td>
                <button
                  className="btn btn-info"
                  style={StyleButtonUpdate}
                  onClick={ModifierIngredient}
                >
                  Mettre à jours
                </button>
                <button className="btn btn-danger">Supprimer </button>
              </td>
            </tr>
            <tr>
              <th scope="row">2</th>
              <td>Oigon</td>
              <td>1</td>
              <td>
                <button className="btn btn-info" style={StyleButtonUpdate}>
                  Mettre à jours
                </button>
                <button className="btn btn-danger">Supprimer </button>
              </td>
            </tr>
            <tr>
              <th scope="row">3</th>
              <td>Ail</td>
              <td>3</td>
              <td>
                <button className="btn btn-info" style={StyleButtonUpdate}>
                  Mettre à jours
                </button>
                <button className="btn btn-danger">Supprimer </button>
              </td>
            </tr>
            <tr>
              <th scope="row">4</th>
              <td>Pomme de terre</td>
              <td>2</td>
              <td>
                <button className="btn btn-info" style={StyleButtonUpdate}>
                  Mettre à jours
                </button>
                <button className="btn btn-danger">Supprimer </button>
              </td>
            </tr>
            <tr>
              <th scope="row">5</th>
              <td>Sel</td>
              <td>1 cuillére de café</td>
              <td>
                <button className="btn btn-info" style={StyleButtonUpdate}>
                  Mettre à jours
                </button>
                <button className="btn btn-danger">Supprimer </button>
              </td>
            </tr>
            <tr>
              <th scope="row">6</th>
              <td>poivre noir </td>
              <td>1 cuillére de café</td>
              <td>
                <button className="btn btn-info" style={StyleButtonUpdate}>
                  Mettre à jours
                </button>
                <button className="btn btn-danger">Supprimer </button>
              </td>
            </tr>
          </tbody>
        </Table>
      </Card>
    </div>
  )
}

export default AjoutIngrédient
